import React from 'react';
import './App.css';
import Navbar from "./Navbar";
import Plateau from "./Plateau";
import Controls from './Controls';
import Modal from "./Modal";
import { throwStatement } from '@babel/types';

class App extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      isWin : "loose",
      currentLine: 0,
      currentCase: 0,
      showModal: 'hidden',
      message: '',
      combinationToFind:this.generateCombination(),
      game:[
      [[],[]],
      [[],[]],
      [[],[]],
      [[],[]],
      [[],[]],
      [[],[]],
      [[],[]],
      [[],[]],
      [[],[]],
      [[],[]],
      ]
    }
    this.change = this.change.bind(this)
    this.undo = this.undo.bind(this)
    this.compare = this.compare.bind(this)
    this.submit = this.submit.bind(this)
    this.closeModal = this.closeModal.bind(this)
    this.generateCombination = this.generateCombination.bind(this)
  }

change(color) {
    const gameBis = [...this.state.game]
    if (this.state.currentCase < 4) {
        gameBis[this.state.currentLine][0].push(color)
        
        this.setState(state => ({
        game: gameBis,
        currentCase: state.currentCase + 1
        }))
    }
}

undo(){
  const gameBis = [...this.state.game]
  if (this.state.currentCase > 0) {
    gameBis[this.state.currentLine][0].pop()
    this.setState(state => ({
      game: gameBis,
      currentCase: state.currentCase -1
    }))
  }
}

submit(){
  let gameBis = [...this.state.game]
  if (this.state.currentCase == 4) {
    let result = this.compare()
    console.log(result);
    for (let index = 0; index < result[0]; index++) {
      gameBis[this.state.currentLine][1].push('black')
    }
    for (let index = 0; index < result[1]; index++) {
      gameBis[this.state.currentLine][1].push('gray')
    }
    this.setState(state => ({
      game:gameBis,
      currentLine: state.currentLine + 1,
      currentCase: 0
    }))

    if (result[0] == 4) {
      this.setState(state => ({
        showModal: "show",
        message: "gagnÃ©"
      }))
    }
    if (this.state.currentLine == 9 && result[0] != 4) {
      this.setState(state => ({
        showModal: "show",
        message: "perdu"
      }))
    }
  }
}

compare(){
  let combination = [...this.state.game[this.state.currentLine][0]]
  let combinationToFind = [...this.state.combinationToFind]
  let good = 0;
  let bad = 0;
  for (let index = 0; index < combination.length; index++) {
    if (combination [index] == combinationToFind[index]) {
      good++;
      combinationToFind[index] = 'x'
      combination[index] = 'a'
    }
  }
  for (let index = 0; index < combinationToFind.length; index++) {
    if (combinationToFind.includes(combination[index])) {
      bad ++
      combinationToFind[combinationToFind.indexOf(combination[index])] ='x'
      combination[index] ='a'
    }
  }
  return[good, bad]
}

closeModal(){
  this.setState({
    showModal:"hidden",
    game:[
      [[],[]],
      [[],[]],
      [[],[]],
      [[],[]],
      [[],[]],
      [[],[]],
      [[],[]],
      [[],[]],
      [[],[]],
      [[],[]],
      ],
      currentLine: 0,
      currentCase: 0
  })
  window.location.reload();
}


generateCombination(){
  let combination = []
  for (let i = 0; i < 4; i++) {
    let random = Math.floor(Math.random() * 6)
    let colors = ["red","blue","yellow","orange","purple","green"]
    combination.push(colors[random])
  }
  return combination
}

  render(){
    return (
    <div className="App">
      <Navbar></Navbar>
      <main>
        <Plateau game={this.state.game} currentLine={this.state.currentLine}> </Plateau>
        <Controls colorize={this.change} undo={this.undo} submit={this.submit}>
        </Controls>
        <Modal message={this.state.message} showModal={this.state.showModal} closeModal={this.closeModal}></Modal>
      </main>
    </div>
  );
  }
  
}

export default App;
